/* This is the main style sheet */

h1, p, div {
    font-family: Roboto;
    font-size: 16px;
    color: #4f4f4f;
}

header {
    grid-area: header;
    background-color: #f8f8f8;
    padding: 0.5% 1% 5% 1%;
  }

header img {
    position: absolute;
}

#virus-one {
    right: 5%;
    top: 10%;
    height: 199px;
}

#virus-two {
    right: 10%;
    top: -5%;
    height: 214px;
}

#virus-three {
    top: 5%;
    left: -4%;
    height: 131px;
}

h1 {
    z-index: 99;
    font-size: 48px;
    /* line-height: 1.04; */
    color: #000000;
    margin-left: 16%;
    margin-top: 3%;
}

#description {
    line-height: 1.25;
    width: 50%;
    margin-right: 54%;
    margin-left: 16%;
  }

#btn-and-slider {
    display: flex;
    align-items: center;
}

#play-btn {
    margin-top: 2%;
    height: 30px;
    width: 30px;
    background-image: url('btn-images//play_button.png');
    background-size: 100%;
    background-repeat: no-repeat;
}

.pause {
    background-image: url('btn-images//pause_button.png') !important;
}

.legend-highlight {
    font-weight: bold;
}

/* slider format */

#time-slider-container {
    padding: 0 2%;
}

#time-slider {
    width: 100%
}

#time-slider-label {
    padding-top: 1%;
    padding-left: 1%;
    display: grid;
    grid-template-columns: repeat(36, 1fr);
}

#time-slider-label > div {
    text-align: center;
    vertical-align: middle;
}

#month-year {
    margin-top: 1%;
    text-align: center;
}

 /* bar chart */
#bar-chart > svg {
    min-height: 700px;
}

#tooltip {
    border-radius: 5px;
    border: solid 2px #bdbdbd;
    background-color: #f2f2f2;
    padding: 0.5%;
    opacity: 0;
}

/* css grid settings */

body, .grid-container {
    display: grid;
    grid-template-areas: "header"
                         "content"
                         "footer";
}

.grid-element.content {
    grid-area: content;
    display: flex;
    justify-content: space-around;
    width: 1200px;
    margin: 0 auto;
}

.grid-container.content > * {
    display: inline;
}

.grid-element.sidebar {
    grid-area: sidebar;
    padding-left: 16%;
    width: 400px;
}

footer.grid-element {
    grid-area: footer;
}

footer strong {
    font-size: 18px;
    margin-top: 2%;
    margin-bottom: 0%;
    display: block;
}

footer p {
    margin: 0;
}
